.gallery {
  background-color: $color-background-default;

  .container {
    padding: 130px 0 0;

    @include vp-1199 {
      padding: 18px 0 0;
    }

    @include vp-767 {
      padding: 7px 0 0;
    }
  }

  p {
    font-size: 16px;
    line-height: 24px;
    padding-left: 120px;
    padding-right: 580px;
    margin-top: 0;
    margin-bottom: 45px;

    @include vp-1199 {
      padding-left: 45px;
      padding-right: 295px;
      margin-bottom: 37px;
      font-size: 14px;
      line-height: 22px;
    }

    @include vp-767 {
      margin-bottom: 65px;
      padding-left: 15px;
      padding-right: 0;
      font-size: 12px;
      line-height: 18px;
    }
  }
}

.gallery__title {
  padding-left: 120px;
  margin-bottom: 25px;
  position: relative;

  @include vp-1199 {
    padding-left: 45px;
    margin-bottom: 14px;
  }

  @include vp-767 {
    padding-left: 15px;
    margin-bottom: 14px;
  }

  h2 {
    margin: 0;
    font-size: 35px;
    font-style: normal;
    font-weight: 800;
    line-height: 35px;
    color: $color-elephant;

    @include vp-1199 {
      font-size: 25px;
    }

    @include vp-767 {
      width: 156px;
      font-size: 22px;
      line-height: 22px;
    }
  }

  &::after {
    content: "Фотогалерея";
    position: absolute;
    top: 0;
    left: 8%;
    transform: translate(0, -25%);
    font-weight: 800;
    font-size: 55px;
    line-height: 55px;
    opacity: 0.03;

    @include vp-1199 {
      font-size: 40px;
      line-height: 40px;
    }

    @include vp-767 {
      font-size: 26px;
      line-height: 26px;
    }
  }
}

.gallery__slider {
  display: grid;
}

.gallery__slide {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 5px;
  width: fit-content;
  height: fit-content;

  img {
    background-color: #7a9b9b;
    width: 100%;
    height: 100%;
  }
}

.gallery__slider-buttons-wrapper {
  position: absolute;
  top: -87px;
  right: 120px;
  display: flex;
  gap: 5px;
  width: fit-content;

  @include vp-1199 {
    right: 45px;
    top: -89px;
  }

  @include vp-767 {
    right: 15px;
    top: -50px;
  }
}

.gallery__slider-next {
  transform: rotate(180deg);
}
